define('taoClientDiagnostic/controller/CompatibilityChecker/diagnostics',[
    'jquery',
    'layout/loading-bar',
    'ui/feedback',
    'taoClientDiagnostic/tools/message',
    'taoClientDiagnostic/tools/diagnostic/diagnostic'
], function ($, loadingBar, feedback, showMessage, diagnosticFactory) {
    'use strict';

    /**
     * @exports
     */
    return {
        start: function(config) {
            var $contentArea = $('.content');

            showMessage('#feedback-box');

            diagnosticFactory(config)
                .on('start', function() {
                    loadingBar.start();
                })
                .on('end', function() {
                    loadingBar.stop();
                })
                .render($contentArea);
        }
    };
});

